import {useParams} from "react-router-dom";
import {useEffect, useState} from "react";
import {Container, Typography} from "@mui/material";
import PageNotFound from "../PageNotFound";
import axios from "axios";
import Markdown from "react-markdown";
import rehypeHighlight from "rehype-highlight";
import rehypeRaw from "rehype-raw";

const ProblemDetail = () => {
    const apiUrl = process.env.REACT_APP_API_URL;

    const {contestId, problemSlug} = useParams();
    const [problem, setProblem] = useState(null);
    const [language, setLanguage] = useState("python");
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const rehypePlugins = [rehypeHighlight, rehypeRaw]

    useEffect(() => {
        setLoading(true);
        axios.get(`${apiUrl}/scribo/problems/${problemSlug}`)
            .then(response => {
                setProblem(response.data);
                setLoading(false);
            })
            .catch(error => {
                console.error("There was an error fetching the problem details:", error);
                setError(error);
                setLoading(false);
            })
    }, [problemSlug]);

    if (loading) return <div>Loading...</div>
    if (error) return <div>Error loading problem details.</div>
    if (!problem) {
        return <PageNotFound/>
    }

    return (<Container className="markdown-body">
        <br/>
        <Typography variant="h3">{problem.title}</Typography>
        <br/>
        <Markdown className="markdown-body" rehypePlugins={rehypePlugins}>
            {problem.statement}
        </Markdown>
        <br/>
        <Markdown className="markdown-body" rehypePlugins={rehypePlugins}>
            {`\n\n${problem.solution}`}
        </Markdown>

        {problem.snippets && problem.snippets.length > 0 && (
            <Markdown className="markdown-body" rehypePlugins={rehypePlugins}>
                {`\`\`\`python\n${problem.snippets[0].code}\n\`\`\``}
            </Markdown>)}

    </Container>)
}

export default ProblemDetail