import {BrowserRouter, Route, Routes, useLocation, useNavigate} from "react-router-dom";
import PageNotFound from "./components/PageNotFound";
import LeetCode from "./components/leetcode/LeetCode";
import ProblemDetail from "./components/leetcode/ProblemDetail";
import NavigationBar from "./components/NavigationBar";
import HomePage from "./components/HomePage";
import Articles from "./components/articles/Articles";
import ArticleDetail from "./components/articles/ArticleDetail";
import {useEffect} from "react";
import ReactGA from "react-ga4";

const GA_MEASUREMENT_ID = process.env.REACT_APP_GA_MEASUREMENT_ID;

function App() {

    ReactGA.initialize(GA_MEASUREMENT_ID);

    useEffect(() => {
        const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');

        const updateHighlightTheme = () => {
            const theme = mediaQuery.matches
                ? 'github-dark.min.css' // Dark mode theme
                : 'github.min.css'; // Light mode theme
            document.getElementById('highlightTheme').href = `https://cdnjs.cloudflare.com/ajax/libs/highlight.js/11.8.0/styles/${theme}`;
        };

        mediaQuery.addEventListener('change', updateHighlightTheme);
        updateHighlightTheme(); // Initial theme update

        // Cleanup function to remove the event listener when the component unmounts
        return () => mediaQuery.removeEventListener('change', updateHighlightTheme);
    }, []);

    return (
        <BrowserRouter>
            <RouteTracker/>
            <RedirectHandler />
            <NavigationBar/>

            {/* Routes Setup */}
            <Routes>
                <Route path="/" element={<HomePage/>}/>

                {/* Routes for LeetCode related */}
                <Route path="/leetcode" element={<LeetCode/>}/>
                <Route path="/problems/:problemSlug" element={<ProblemDetail/>}/>
                <Route path="/problems/:contestId/:problemId" element={<ProblemDetail/>}/>

                {/* Routes for Articles related */}
                <Route path="/articles" element={<Articles/>}/>
                <Route path="/articles/:articleId" element={<ArticleDetail/>}/>

                {/* Routes for customized 404 Page Not Found */}
                <Route path="*" element={<PageNotFound/>}/>
            </Routes>

        </BrowserRouter>
    );
}

function RouteTracker() {
    const location = useLocation();

    useEffect(() => {
        ReactGA.send({hitType: "pageview", page: location.pathname})
    }, [location]);

    return null;
}

function RedirectHandler() {
    const navigate = useNavigate();

    useEffect(() => {
        const notFoundPath = localStorage.getItem("NOT_FOUND_PATH");
        if (notFoundPath) {
            navigate(notFoundPath);
            localStorage.removeItem("NOT_FOUND_PATH");
        }
    }, [navigate]);
    return null;
}

export default App;
