import {useState} from "react";
import {
    AppBar,
    Box,
    Button,
    Drawer,
    IconButton,
    List,
    ListItem,
    ListItemText,
    Toolbar,
} from "@mui/material";
import {Link} from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';
import logo from '../assets/logo512.png'

function NavigationBar() {
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);

    const handleDrawerToggle = () => {
        setIsDrawerOpen(!isDrawerOpen);
    }

    return (
        <>
            <AppBar position="static">
                <Toolbar sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    {/* Site Name or Logo */}
                    <Link to="/" style={{ display: 'inline-flex', alignItems: 'center' }}>
                        <img src={logo} alt="Lucky Codes Logo" style={{ height: 50 }} />
                    </Link>


                    {/* Navigation Buttons - Hidden on xs to sm screens */}
                    <Box sx={{display: {xs: 'none', md: 'flex'}, flexGrow: 0}}>
                        <Button color="inherit" component={Link} to="/leetcode">LeetCode</Button>
                        <Button color="inherit" component={Link} to="/articles">Articles</Button>
                        {/*<Button color="inherit" component={Link} to="/projects">Projects</Button>*/}
                        {/*<Button color="inherit" component={Link} to="/about-me">About Me</Button>*/}
                    </Box>

                    {/* Menu Icon - Visible on xs to sm screens */}
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={handleDrawerToggle}
                        sx={{ ml: { xs: 2, sm: 2 }, mr: 2, display: {md: 'none'}}}
                    >
                        <MenuIcon/>
                    </IconButton>
                </Toolbar>
            </AppBar>

            {/* Drawer */}
            <Drawer
                anchor="left"
                open={isDrawerOpen}
                onClose={handleDrawerToggle}
            >
                <Box
                    sx={{width: 250}}
                    role="presentation"
                    onClick={handleDrawerToggle}
                    onKeyDown={handleDrawerToggle}
                >
                    <List>
                        {['LeetCode', 'Articles'].map((text, index) => (
                            <ListItem button key={text} component={Link}
                                      to={`/${text.toLowerCase().replace(/\s+/g, '-')}`}>
                                <ListItemText primary={text}/>
                            </ListItem>
                        ))}
                    </List>
                </Box>
            </Drawer>
        </>
    )
}

export default NavigationBar;