import React, {useEffect, useState} from 'react';
import {
    Accordion,
    AccordionDetails,
    AccordionSummary,
    Chip,
    Container,
    List,
    ListItem,
    ListItemText,
    Typography
} from '@mui/material';
import {Link, useParams} from "react-router-dom";
import axios from "axios";

const difficultyStyles = {
    easy: {backgroundColor: "green", color: "white"},
    medium: {backgroundColor: "orange", color: "white"},
    hard: {backgroundColor: "red", color: "white"}
};

const LeetCode = () => {
    const apiUrl = process.env.REACT_APP_API_URL;
    const {contestId, problemId} = useParams();
    const [usualProblems, setUsualProblems] = useState(null);
    const [contests, setContests] = useState(null)
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        setLoading(true);
        axios.get(`${apiUrl}/scribo/problems/no-contest/`)
            .then(response => {
                setUsualProblems(response.data);
                setLoading(false);
            })
            .catch(error => {
                console.error("There was an error fetching the problem list:", error);
                setError(error);
                setLoading(false);
            })
    }, []);

    useEffect(() => {
        setLoading(true);
        axios.get(`${apiUrl}/scribo/contests/`)
            .then(response => {
                setContests(response.data);
                setLoading(false);
            })
            .catch(error => {
                console.error("There was an error fetching the contest list:", error);
                setError(error);
                setLoading(false);
            })
    }, []);


    return (
        <Container className="markdown-body" sx={{my: 4}}>
            <Typography variant="h2" gutterBottom>
                LeetCode Solutions
            </Typography>

            {/* Contest Solutions */}
            <Typography variant="h3" gutterBottom>Contest</Typography>
            {
                contests && contests.map((contest, index) => (

                    <Accordion className="markdown-body" key={index}>
                        <AccordionSummary className="markdown-body">
                            <Typography>{contest.contest_type} {contest.title}</Typography>
                        </AccordionSummary>
                        {contest.problems && contest.problems.length > 0 && (<AccordionDetails>
                            <List className="markdown-body">
                                {
                                    contest.problems.map((problem, pIndex) => (
                                        <ListItem button component={Link}
                                                  to={`/problems/${problem.slug}`}
                                                  key={pIndex}>
                                            <ListItemText primary={problem.title}/>
                                            <Chip label={problem.difficulty} size="small"
                                                  sx={{...difficultyStyles[problem.difficulty], marginLeft: "auto"}}/>
                                        </ListItem>
                                    ))
                                }
                            </List>
                        </AccordionDetails>)}
                    </Accordion>
                ))
            }
            {/* Usual Problems */}
            <Typography variant="h3" sx={{mt: 2}}>Problems</Typography>
            <List>
                {
                    usualProblems && usualProblems.map((problem, index) => (
                        <ListItem button component={Link} to={`/problems/${problem.slug}`} key={index}>
                            <ListItemText primary={problem.title}/>
                            <Chip label={problem.difficulty} size="small"
                                  sx={{...difficultyStyles[problem.difficulty], marginLeft: "auto"}}/>
                        </ListItem>
                    ))
                }
            </List>
        </Container>
    )
};


export default LeetCode;
